.task{
    display: flex;
    justify-content: space-between;
    max-width: 400px;
    margin: 0 auto;
    border-bottom:  1px solid #ccc;
    padding: 1em;
}

.details{
    font-size:1.2em;
    margin-bottom: 1em;
}


.action{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.action i{
    margin-bottom: .5em;
    font-size: 1.2em;
    cursor: pointer;
    background-color: #282c34;
    color: #FFF;
    padding: .4em;
    transition: .5s;
}

.action i:hover{
    color: #61dafb;
}
